@use 'base/variables' as v;
@use 'base/mixins' as m;

.places {

    &__grid {

        display: grid;
        grid-template-columns: repeat(4, 30rem);
        column-gap: 4rem;
        @include m.scrollX;

        @include m.tablet { 
            @include m.grid(2, 2rem);
        }

        @include m.desktop { 
            @include m.grid(4, 2rem);
        }

    }
}

.place {

    &__image {
        border-radius: 1rem;
    }

    &__name {
        font-size: 1.8rem;
    }
}