@use 'base/variables' as v;
@use 'base/mixins' as m;

.experiences {

    &__list {

        display: grid;
        grid-template-columns: repeat(3, 30rem);
        column-gap: 2rem;
        @include m.scrollX;

        @include m.tablet { 
            @include m.grid(3, 2rem);
        }
    }

}

.experience {

    &__image {
        border-radius: 1rem;
    }

    &__title {
        font-weight: 700;
        font-size: 2rem;
    }

    &__description {
        font-size: 1.8rem;
    }

}